<template>
  <div id="pas">
    <el-card class="card-box">
      <p>Why not quickly generate a secure password?</p>
      <div class="input">
        密码位数:<el-input class="passwordnum" v-model="num"></el-input>
        <el-button @click="create">生成</el-button>
      </div>
      <div class="output">
        生成密码:<el-input class="passwordnum" v-model="password"></el-input>
        <el-button id="copy-btn" @click="copyToClipboard">拷贝</el-button>
        <!-- 拷贝键 -->
      </div>
      <div class="detection">
        <div>
          <el-progress :percentage="20" status="exception" v-if="detection.score <= 1"></el-progress>
          <el-progress :percentage="60" status="warning" color="#559eff" v-else-if="detection.score === 2"></el-progress>
          <el-progress :percentage="100" status="success" v-else></el-progress>
        </div>
        <div>
          <p v-if="detection.warning" style="color: red">{{ detection.warning }}</p>
        </div>
      </div>
      <div class="setting">
        <el-checkbox v-model="state.bcode">包含大写字母</el-checkbox>
        <el-checkbox v-model="state.scode">包含小写字母</el-checkbox>
        <el-checkbox v-model="state.ncode">包含数字</el-checkbox>
      </div>
    </el-card>
  </div>
</template>

<script>
import zxcvbn from 'zxcvbn'

export default {
  name: 'pas',
  data() {
    return {
      password: '',
      num: 20,
      state: {
        bcode: true,
        scode: true,
        ncode: true
      },
      detection: {
        score: 0,
        warning: ''
      }
    }
  },
  watch: {
    password(e) {
      const result = zxcvbn(e)
      this.detection.score = result.score // 密码强度评分（0-4）
      this.detection.warning = result.feedback.warning // 强度警告信息
      // console.log(this.detection.score)
    }
  },
  methods: {
    create() {
      this.password = ''
      if (this.num > 20 && this.num < 0) return

      if (this.state.bcode === true) {
        for (let i = 0; i < 20; i++) {
          // 随机生成小写字母的ASCII码
          let letterCode = Math.floor(Math.random() * 26) + 65 // ASCII码范围：A-Z
          let str = String.fromCharCode(letterCode)
          this.password = this.password + str
        }
        // console.log(this.password)
      }

      if (this.state.scode === true) {
        for (let i = 0; i < 20; i++) {
          // 随机生成小写字母的ASCII码
          let letterCode = Math.floor(Math.random() * 26) + 97 // ASCII码范围：a-z
          let str = String.fromCharCode(letterCode)
          this.password = this.password + str
        }
        // console.log(this.password)
      }

      if (this.state.ncode === true) {
        for (let i = 0; i < 20; i++) {
          // 随机生成小写字母的ASCII码
          let letterCode = Math.floor(Math.random() * 10) + 48 // ASCII码范围：0-9
          let str = String.fromCharCode(letterCode)
          this.password = this.password + str
        }
        // console.log(this.password)
      }

      let subStr = this.randomizeString(this.password).substring(0, this.num)
      console.log(subStr)
      this.password = subStr
    },
    // 定义一个函数来对字符串进行随机重组
    randomizeString(str) {
      // 将字符串转换为字符数组
      let charArray = str.split('')

      // 遍历字符数组，从最后一个字符开始向前遍历
      for (let i = charArray.length - 1; i > 0; i--) {
        // 生成一个随机索引，范围是从 0 到当前字符索引
        let randomIndex = Math.floor(Math.random() * (i + 1))

        // 交换当前字符与随机索引处的字符
        let temp = charArray[i]
        charArray[i] = charArray[randomIndex]
        charArray[randomIndex] = temp
      }

      // 将字符数组重新组合为一个新的字符串
      let randomizedString = charArray.join('')

      // 返回随机重组后的字符串
      return randomizedString
    },
    copyToClipboard() {
      const passwordInput = document.querySelector('.output .passwordnum input')
      passwordInput.select()
      passwordInput.setSelectionRange(0, passwordInput.value.length)
      document.execCommand('copy')
    }
  }
}
</script>

<style lang="scss" scoped>
#app {
  display: flex;
}

.card-box {
  width: 400px;
  height: 350px;
  margin: auto;
  background-color: #bad5fe;

  div {
    margin: 10px 0;

    button,
    .el-input {
      margin-left: 10px;
    }
  }
}

.passwordnum {
  width: 200px;
}
</style>
